import React, { createContext, useState, useContext, useEffect, ReactNode, useCallback } from 'react';
import { Property, CreatePropertyData, SearchFilters } from '../types';
import { useAuth } from './AuthContext';
import { apiGet, apiJson } from '../config';
import toast from 'react-hot-toast';

interface PropertyContextType {
  properties: Property[];
  userProperties: Property[];
  loading: boolean;
  createProperty: (propertyData: CreatePropertyData) => Promise<void>;
  searchProperties: (filters?: SearchFilters) => Promise<Property[]>;
  fetchProperties: () => Promise<void>;
  fetchPropertyById: (id: string) => Promise<Property | null>;
  deleteProperty: (propertyId: string) => Promise<void>;
  updateProperty: (propertyId: string, updates: Partial<Property>) => Promise<Property>;
  fetchUserProperties: () => Promise<void>;
}

const PropertyContext = createContext<PropertyContextType | undefined>(undefined);

export const useProperty = () => {
  const context = useContext(PropertyContext);
  if (context === undefined) {
    throw new Error('useProperty must be used within a PropertyProvider');
  }
  return context;
};

interface PropertyProviderProps {
  children: ReactNode;
}

export const PropertyProvider: React.FC<PropertyProviderProps> = ({ children }) => {
  const [properties, setProperties] = useState<Property[]>([]);
  const [userProperties, setUserProperties] = useState<Property[]>([]);
  const [loading, setLoading] = useState(false);
  const { user } = useAuth();

  // Rcuprer toutes les proprits - CORRIG
  const fetchProperties = useCallback(async () => {
    setLoading(true);
    try {
      const data = await apiGet('/api/properties/get_all');
      
      if (data.success && data.data) {
        const propertiesData = Array.isArray(data.data) ? data.data : [];
        setProperties(propertiesData);
      } else {
        setProperties([]);
      }
    } catch (error) {
      console.error('Erreur fetchProperties:', error);
      toast.error('Erreur lors du chargement des proprits');
      setProperties([]);
    } finally {
      setLoading(false);
    }
  }, []);

  // Rcuprer une proprit par ID - CORRIG
  const fetchPropertyById = async (id: string): Promise<Property | null> => {
    try {
      const data = await apiGet(`/api/properties/get_by_id/${id}`);
      
      if (data.success && data.data) {
        return data.data;
      }
      
      return null;
    } catch (error) {
      console.error('Erreur fetchPropertyById:', error);
      toast.error('Erreur lors du chargement de la proprit');
      return null;
    }
  };

  // Crer une nouvelle proprit - CORRIG
  const createProperty = async (propertyData: CreatePropertyData) => {
    setLoading(true);
    try {
      if (!user) {
        throw new Error('Vous devez tre connect pour crer une proprit');
      }

      console.log('Donnes envoyes  l\'API:', {
        ...propertyData,
        ownerId: user.id
      });

      const data = await apiJson('/api/properties/create', 'POST', propertyData);
      
      if (!data.success) {
        throw new Error(data.message || 'Erreur lors de la cration');
      }
      
      const newProperty = data.data;
      
      setProperties(prev => [newProperty, ...prev]);
      setUserProperties(prev => [newProperty, ...prev]);
      
      toast.success('Proprit cre avec succs !');
    } catch (error) {
      console.error('Erreur createProperty:', error);
      toast.error(error instanceof Error ? error.message : 'Erreur lors de la cration de la proprit');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Rcuprer les proprits de l'utilisateur - CORRIG
  const fetchUserProperties = useCallback(async () => {
    if (!user) {
      setUserProperties([]);
      return;
    }

    setLoading(true);
    try {
      const data = await apiGet('/api/properties/get_by_user');
      
      if (data.success && data.data) {
        const propertiesData = Array.isArray(data.data) ? data.data : [];
        setUserProperties(propertiesData);
      } else {
        setUserProperties([]);
      }
    } catch (error: any) {
      console.error('Erreur fetchUserProperties:', error);
      // Ne pas afficher d'erreur si c'est une erreur 401 (gre par config.ts)
      if (!error.message?.includes('Token invalide') && !error.message?.includes('401')) {
        toast.error('Erreur lors du chargement de vos proprits');
      }
      setUserProperties([]);
    } finally {
      setLoading(false);
    }
  }, [user]);

  // Modifier une proprit - CORRIG
  const updateProperty = async (propertyId: string, updates: Partial<Property>): Promise<Property> => {
    setLoading(true);
    try {
      const data = await apiJson(`/api/properties/update/${propertyId}`, 'PUT', updates);
      
      if (!data.success) {
        throw new Error(data.message || 'Erreur lors de la mise  jour');
      }
      
      const updatedProperty = data.data;
      
      setProperties(prev => prev.map(p => p.id === propertyId ? updatedProperty : p));
      setUserProperties(prev => prev.map(p => p.id === propertyId ? updatedProperty : p));
      
      toast.success('Proprit modifie avec succs');
      return updatedProperty;
    } catch (error: any) {
      toast.error('Erreur lors de la modification de la proprit');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Supprimer une proprit - CORRIG
  const deleteProperty = async (propertyId: string): Promise<void> => {
    setLoading(true);
    try {
      const data = await apiJson(`/api/properties/delete/${propertyId}`, 'DELETE');
      
      if (!data.success) {
        throw new Error(data.message || 'Erreur lors de la suppression');
      }
      
      setProperties(prev => prev.filter(p => p.id !== propertyId));
      setUserProperties(prev => prev.filter(p => p.id !== propertyId));
      
      toast.success('Proprit supprime avec succs');
    } catch (error: any) {
      toast.error('Erreur lors de la suppression de la proprit');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Rechercher des proprits avec filtres - CORRIG
  const searchProperties = async (filters: SearchFilters = {}): Promise<Property[]> => {
    setLoading(true);
    try {
      const queryParams = new URLSearchParams();
      
      Object.entries(filters).forEach(([key, value]) => {
        if (value !== undefined && value !== null && value !== '') {
          if (Array.isArray(value)) {
            value.forEach(v => queryParams.append(key, v));
          } else {
            queryParams.append(key, value.toString());
          }
        }
      });

      const url = `/api/properties/search?${queryParams.toString()}`;
      const data = await apiGet(url);
      
      if (data.success && data.data) {
        return Array.isArray(data.data) ? data.data : [];
      }
      return [];
    } catch (error) {
      console.error('Erreur searchProperties:', error);
      toast.error('Erreur lors de la recherche des proprits');
      return [];
    } finally {
      setLoading(false);
    }
  };

  // Effets
  useEffect(() => {
    fetchProperties();
  }, [fetchProperties]);

  useEffect(() => {
    fetchUserProperties();
  }, [fetchUserProperties]);

  const value: PropertyContextType = {
    properties,
    userProperties,
    loading,
    createProperty,
    searchProperties,
    fetchProperties,
    fetchPropertyById,
    deleteProperty,
    updateProperty,
    fetchUserProperties,
  };

  return (
    <PropertyContext.Provider value={value}>
      {children}
    </PropertyContext.Provider>
  );
};