// src/services/cinVerificationService.ts
import Tesseract from 'tesseract.js';

export interface CINData {
  numeroCIN: string;
  nom: string;
  prenoms: string;
  dateNaissance: string;
  lieuNaissance: string;
  adresse: string;
  profession: string;
  pere: string;
  mere: string;
  dateDelivrance: string;
  lieuDelivrance: string;
}

export interface CINVerificationResult {
  success: boolean;
  message: string;
  validationErrors: string[];
}

class CINVerificationService {

  // Extraction OCR avec Tesseract.js - VERSION CORRIGE
  private async extractTextFromImage(imageFile: File): Promise<string> {
    try {
      console.log(' Dbut de l\'extraction OCR...');
      
      const worker = await Tesseract.createWorker('fra+eng', 1, {
        logger: m => {
          if (m.status === 'recognizing text') {
            console.log(`OCR Progress: ${(m.progress * 100).toFixed(1)}%`);
          }
        }
      });
      
      // Configurer Tesseract pour de meilleurs rsultats (paramtres corrigs)
      await worker.setParameters({
        // Utiliser des paramtres valides pour TypeScript
        tessedit_char_whitelist: '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz -.,\'/',
        tessedit_ocr_engine_mode: 1, // Neural nets LSTM (number au lieu de string)
      } as any); // Utiliser "as any" pour contourner les restrictions TypeScript
      
      const result = await worker.recognize(URL.createObjectURL(imageFile));      
      await worker.terminate();
      
      console.log(' Extraction OCR termine');
      return result.data.text;
    } catch (error) {
      console.error(' Erreur OCR:', error);
      throw new Error('chec de l\'extraction du texte de la CIN');
    }
  }

  // Nettoyage et parsing du texte extrait - VERSION CORRIGE (ESLint)
  private parseCINData(ocrText: string, cinNumberSaisi: string, side: 'recto' | 'verso'): Partial<CINData> {
    console.log(` Parsing du texte OCR (${side})...`);
    
    // Nettoyer le texte OCR plus agressivement
    const cleanedText = ocrText
      .replace(/\n/g, ' ')
      .replace(/\s+/g, ' ')
      .replace(/[^\w\s-.,/-]/g, ' ') // Garder seulement les caractres utiles (corrig ESLint)
      .trim();
    
    console.log(` Texte OCR ${side} nettoy:`, cleanedText);
    
    const cinData: Partial<CINData> = {};

    // Toujours utiliser le numro saisi comme fallback
    cinData.numeroCIN = cinNumberSaisi;
    console.log(' Numro CIN (saisi):', cinData.numeroCIN);

    // Patterns amliors pour les champs CIN - plus flexibles (corrigs ESLint)
    const patterns = {
      nom: [
        /nom[:\s]*([a-zA-Z-\s-]{2,})/i,
        /anarana[:\s]*([a-zA-Z-\s-]{2,})/i,
        /([A-Z][a-zA-Z-\s-]{2,})(?=\s*prnom)/i // Capture le nom avant "prnom"
      ],
      prenoms: [
        /pr[e]noms?[:\s]*([a-zA-Z-\s-]{2,})/i,
        /fanampin['\s]anarana[:\s]*([a-zA-Z-\s-]{2,})/i,
        /(?:prnom|prenoms)[\s:]+([a-zA-Z-\s-]{2,})/i
      ],
      dateNaissance: [
        /n[e][e]?\s*(le)?[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i, // Corrig ESLint
        /teraka[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i, // Corrig ESLint
        /naissance[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i, // Corrig ESLint
        /(\d{2}[/.-]\d{2}[/.-]\d{4})/ // Pattern date gnrique (corrig ESLint)
      ],
      lieuNaissance: [
        /[a]\s*[:\s]*([a-zA-Z-\s-,]{2,})/i,
        /tao[:\s]*([a-zA-Z-\s-,]{2,})/i,
        /lieu[:\s]*naissance[:\s]*([a-zA-Z-\s-,]{2,})/i
      ],
      adresse: [
        /domicile[:\s]*([a-zA-Z-0-9\s-,]{5,})/i,
        /fenenana[:\s]*([a-zA-Z-0-9\s-,]{5,})/i,
        /adresse[:\s]*([a-zA-Z-0-9\s-,]{5,})/i
      ],
      profession: [
        /profession[:\s]*([a-zA-Z-\s-]{2,})/i,
        /asa[:\s]*([a-zA-Z-\s-]{2,})/i,
        /m[e]tier[:\s]*([a-zA-Z-\s-]{2,})/i
      ],
      pere: [
        /p[e]re[:\s]*([a-zA-Z-\s-]{2,})/i,
        /ray[:\s]*([a-zA-Z-\s-]{2,})/i,
        /pre[:\s]*([a-zA-Z-\s-]{2,})/i
      ],
      mere: [
        /m[e]re[:\s]*([a-zA-Z-\s-]{2,})/i,
        /reny[:\s]*([a-zA-Z-\s-]{2,})/i,
        /mre[:\s]*([a-zA-Z-\s-]{2,})/i
      ],
      dateDelivrance: [
        /d[e]livr[e]e?[:\s]*(le)?[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i, // Corrig ESLint
        /tamin['\s]ny[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i, // Corrig ESLint
        /dlivrance[:\s]*([0-9]{2}[/.-][0-9]{2}[/.-][0-9]{4})/i // Corrig ESLint
      ],
      lieuDelivrance: [
        /[a]\s*[:\s]*([a-zA-Z-\s-,]{2,})(?=.*d[e]livr)/i,
        /faita[:\s]*([a-zA-Z-\s-,]{2,})/i,
        /lieu[:\s]*dlivrance[:\s]*([a-zA-Z-\s-,]{2,})/i
      ]
    };

    // Rechercher chaque champ avec les patterns
    Object.entries(patterns).forEach(([field, patternList]) => {
      for (const pattern of patternList) {
        const match = cleanedText.match(pattern);
        if (match) {
          // Prendre le premier groupe de capture (index 1) ou le match complet
          const value = (match[1] || match[0]).trim();
          if (value && value.length > 1 && !cinData[field as keyof CINData]) {
            cinData[field as keyof CINData] = value;
            console.log(` ${field} trouv:`, value);
            break;
          }
        }
      }
    });

    return cinData;
  }

  // Validation des donnes extraites - VERSION SIMPLIFIE
  private validateCINData(cinData: Partial<CINData>, cinNumberSaisi: string): { isValid: boolean; errors: string[] } {
    const errors: string[] = [];

    if (!cinData.numeroCIN || !/^\d{12}$/.test(cinData.numeroCIN)) {
      errors.push('Numro CIN invalide ou manquant');
    }

    // Validation plus permissive pour les tests
    if (!cinData.nom || cinData.nom.length < 2) {
      console.warn(' Nom manquant ou trop court');
    }

    if (!cinData.prenoms || cinData.prenoms.length < 2) {
      console.warn(' Prnoms manquants ou trop courts');
    }

    return {
      isValid: errors.length === 0,
      errors
    };
  }

  // Vrification de la qualit de l'image
  private async verifyImageQuality(imageFile: File): Promise<number> {
    return new Promise((resolve) => {
      const img = new Image();
      img.onload = () => {
        let qualityScore = 70;
        
        // Score bas sur la taille et la rsolution
        if (imageFile.size > 500000) { // > 500KB
          qualityScore += 10;
        }
        if (imageFile.size > 1000000) { // > 1MB
          qualityScore += 10;
        }
        
        // Score bas sur la rsolution
        if (img.width > 1000 && img.height > 800) {
          qualityScore += 10;
        }
        
        resolve(Math.min(qualityScore, 95));
      };
      img.onerror = () => resolve(60); // Score minimum en cas d'erreur
      img.src = URL.createObjectURL(imageFile);
    });
  }

  // Vrification de cohrence recto-verso
  private verifyRectoVersoConsistency(rectoData: Partial<CINData>, versoData: Partial<CINData>): boolean {
    // Pour le moment, toujours retourner true pour permettre la vrification
    // mme si l'OCR choue partiellement
    return true;
  }

  // Vrification principale avec IA - VERSION ROBUSTE
   // Validation simple du numro CIN
  private validateCINNumber(cinNumber: string): boolean {
    return /^\d{12}$/.test(cinNumber);
  }

  // Validation des fichiers images
  private validateImages(rectoImage: File, versoImage: File): string[] {
    const errors: string[] = [];

    if (!rectoImage || !versoImage) {
      errors.push('Les deux images (recto et verso) sont requises');
      return errors;
    }

    // Vrifier le type de fichier
    if (!rectoImage.type.startsWith('image/')) {
      errors.push('Le recto doit tre une image valide');
    }

    if (!versoImage.type.startsWith('image/')) {
      errors.push('Le verso doit tre une image valide');
    }

    // Vrifier la taille (max 5MB)
    if (rectoImage.size > 5 * 1024 * 1024) {
      errors.push('Le recto ne doit pas dpasser 5MB');
    }

    if (versoImage.size > 5 * 1024 * 1024) {
      errors.push('Le verso ne doit pas dpasser 5MB');
    }

    return errors;
  }

  // Vrification simple sans IA
  async verifyCIN(
    cinNumber: string, 
    cinRectoImage: File, 
    cinVersoImage: File
  ): Promise<CINVerificationResult> {
    try {
      console.log(' Dbut de la vrification CIN simple...');

      // 1. Valider le numro CIN
      if (!this.validateCINNumber(cinNumber)) {
        return {
          success: false,
          message: 'Numro CIN invalide',
          validationErrors: ['Le numro CIN doit contenir exactement 12 chiffres']
        };
      }

      // 2. Valider les images
      const imageErrors = this.validateImages(cinRectoImage, cinVersoImage);
      if (imageErrors.length > 0) {
        return {
          success: false,
          message: 'Problmes avec les images',
          validationErrors: imageErrors
        };
      }

      // 3. Vrification russie
      console.log(' Vrification CIN russie');

      return {
        success: true,
        message: 'Vrification CIN russie !',
        validationErrors: []
      };

    } catch (error) {
      console.error(' Erreur lors de la vrification CIN:', error);
      return {
        success: false,
        message: 'Erreur technique lors de la vrification',
        validationErrors: ['Erreur systme']
      };
    }
  }
}

export const cinVerificationService = new CINVerificationService();