// config.ts
import { getStorage, setStorage, removeStorage } from './utils/storage';
import { formatErrorForDisplay } from './utils/errorHandler';

// URL de base : On utilise la variable d'env ou la valeur en dur, sans slash final
const API_BASE_URL = (process.env.REACT_APP_API_URL || 'https://projet-stage-backend.vercel.app').replace(/\/+$/, '');

console.log(' Configuration API:', API_BASE_URL);

const buildUrl = (endpoint: string): string => {
  // Nettoyer l'endpoint : enlever tous les slashes en dbut et fin
  const cleanEndpoint = endpoint.replace(/^\/+|\/+$/g, '');
  // Nettoyer l'URL de base : enlever le slash de fin s'il existe
  const cleanBaseUrl = API_BASE_URL.replace(/\/+$/, '');
  // Construire l'URL finale en vitant les doubles slashes
  let finalUrl = `${cleanBaseUrl}/${cleanEndpoint}`;
  
  // Remplacer tous les squences de plus d'un slash par un seul
  finalUrl = finalUrl.replace(/([^:])\/\//g, '$1/');
  
  // S'assurer que le protocole est correct (http:// ou https://)
  finalUrl = finalUrl.replace(/(https?:)\/+/g, '$1//');
  
  console.log('URL construite:', finalUrl); // Pour le dbogage
  return finalUrl;
};

// Variable globale pour grer la dconnexion en cas d'erreur 401
let onUnauthorizedCallback: (() => void) | null = null;

export const setUnauthorizedCallback = (callback: () => void) => {
  onUnauthorizedCallback = callback;
};

export const apiGet = async (url: string) => {
  const fullUrl = buildUrl(url);
  
  const token = getStorage('auth_token');
  const headers: HeadersInit = {
    'Content-Type': 'application/json',
    'Accept': 'application/json' // Force la demande de JSON
  };
    
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }
  
  try {
    const response = await fetch(fullUrl, {
      method: 'GET',
      headers,
    });
    
    // Si le serveur renvoie une erreur HTML (404/500), on capture l'erreur ici avant le JSON.parse
    if (!response.ok) {
       const errorText = await response.text();
       console.error(' Server Error Response:', errorText);
       
       // Grer les erreurs 401 (Unauthorized) - Token invalide ou expir
       if (response.status === 401) {
         console.warn(' Token invalide ou expir, dconnexion...');
         // Dclencher la dconnexion si callback disponible
         if (onUnauthorizedCallback) {
           onUnauthorizedCallback();
         }
       }
       
       // Essayer de parser le JSON pour obtenir le message d'erreur dtaill
       try {
         const errorJson = JSON.parse(errorText);
         const error = new Error(errorJson.message || errorJson.error || 'Une erreur est survenue');
         (error as any).response = { status: response.status, data: errorJson };
         throw error;
       } catch (e: any) {
         if (e.message && e.response) {
           throw e;
         }
         const error = new Error(formatErrorForDisplay({ response: { status: response.status }, message: e.message || 'Une erreur est survenue' }));
         (error as any).response = { status: response.status };
         throw error;
       }
    }
    
    const text = await response.text();
    try {
      return JSON.parse(text);
    } catch (e) {
      console.error(' Invalid JSON received:', text.substring(0, 100));
      throw new Error('Le serveur a renvoy un format invalide (HTML au lieu de JSON)');
    }
  } catch (error: any) {
    console.error(' Fetch error:', error);
    // Si l'erreur n'a pas dj t formate, la formater
    if (error && !error.response) {
      const formattedError = new Error(formatErrorForDisplay(error));
      (formattedError as any).response = { status: 0 };
      throw formattedError;
    }
    throw error;
  }
};

export const apiUpload = async (url: string, formData: FormData) => {
  const fullUrl = buildUrl(url);
  const token = getStorage('auth_token');
  const headers: HeadersInit = {
    'Accept': 'application/json'
  };

  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }

  try {
    const response = await fetch(fullUrl, {
      method: 'POST',
      headers,
      body: formData,
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error(' Server Error Response:', errorText);
      
      // Grer les erreurs 401 (Unauthorized) - Token invalide ou expir
      if (response.status === 401) {
        console.warn(' Token invalide ou expir, dconnexion...');
        // Dclencher la dconnexion si callback disponible
        if (onUnauthorizedCallback) {
          onUnauthorizedCallback();
        }
      }
      
      // Essayer de parser le JSON pour obtenir le message d'erreur dtaill
      try {
        const errorJson = JSON.parse(errorText);
        const error = new Error(errorJson.message || errorJson.error || 'Une erreur est survenue');
        (error as any).response = { status: response.status, data: errorJson };
        throw error;
      } catch (e: any) {
        if (e.message) {
          const error = new Error(formatErrorForDisplay({ response: { status: response.status }, message: e.message }));
          (error as any).response = { status: response.status };
          throw error;
        }
        const error = new Error(formatErrorForDisplay({ response: { status: response.status } }));
        (error as any).response = { status: response.status };
        throw error;
      }
    }

    const text = await response.text();
    try {
      return JSON.parse(text);
    } catch (e) {
      console.error(' Invalid JSON received:', text.substring(0, 100));
      throw new Error('Le serveur a renvoy un format invalide');
    }
  } catch (error: any) {
    console.error(' Upload error:', error);
    // Si l'erreur n'a pas dj t formate, la formater
    if (error && !error.response) {
      const formattedError = new Error(formatErrorForDisplay(error));
      (formattedError as any).response = { status: 0 };
      throw formattedError;
    }
    throw error;
  }
};

