export const setAuthToken = (token: string | null) => {
  if (token) {
    setStorage('auth_token', token);
  } else {
    removeStorage('auth_token');
  }
};

export const getAuthToken = () => {
  return getStorage('auth_token');
};

// Fonction utilitaire pour obtenir l'URL de base de l'API
export const getApiBaseUrl = () => {
  return API_BASE_URL;
};

// Fonction utilitaire pour construire une URL d'image complte
export const getImageUrl = (imageUrl: string | null | undefined): string => {
  if (!imageUrl) return '';
  
  // Si c'est dj une URL complte (http/https), la retourner telle quelle
  if (imageUrl.startsWith('http://') || imageUrl.startsWith('https://') || imageUrl.startsWith('data:')) {
    return imageUrl;
  }
  
  // Si c'est un chemin relatif, construire l'URL complte
  if (imageUrl.startsWith('/')) {
    return `${API_BASE_URL}${imageUrl}`;
  }
  
  // Sinon, ajouter le slash et construire l'URL
  return `${API_BASE_URL}/${imageUrl}`;
};