// services/avatarUploadService.ts
// Utilise exactement la mme mthode que les annonces et proprits : upload via /api/upload/image
import { apiUpload } from '../config';

// Upload function pour les avatars - mme mthode que les annonces/proprits
export const uploadAvatar = async (file: File): Promise<string> => {
  // Validate file type (mme validation que les annonces/proprits)
  const validTypes = ['image/jpeg', 'image/png', 'image/webp'];
  if (!validTypes.includes(file.type)) {
    throw new Error('Format d\'image non support. Utilisez JPEG, PNG ou WebP');
  }

  // Valider la taille (max 5MB - mme que les annonces/proprits)
  if (file.size > 5 * 1024 * 1024) {
    throw new Error('L\'image ne doit pas dpasser 5MB');
  }

  try {
    // Utiliser exactement la mme mthode que les annonces/proprits
    const formData = new FormData();
    formData.append('image', file);
    
    const data = await apiUpload('/api/upload/image', formData);
    
    if (!data.success) {
      throw new Error(data.message || 'Erreur lors de l\'upload');
    }
    
    // Retourner l'URL (mme format que les annonces/proprits)
    const imageUrl = data.data?.url || data.data?.path || '';
    
    if (!imageUrl) {
      throw new Error('Aucune URL d\'image valide reue du serveur');
    }

    // Pas de limite de longueur - les URLs sont stockes en TEXT dans la base de donnes
    // Exactement comme pour les annonces et proprits
    return imageUrl;
  } catch (error: any) {
    console.error('Erreur upload avatar:', error);
    throw new Error(error.message || 'Impossible d\'uploader l\'avatar');
  }
};

// Upload function publique pour l'inscription (sans authentification)
export const uploadAvatarPublic = async (file: File): Promise<string> => {
  // Validate file type
  const validTypes = ['image/jpeg', 'image/png', 'image/webp'];
  if (!validTypes.includes(file.type)) {
    throw new Error('Format d\'image non support. Utilisez JPEG, PNG ou WebP');
  }

  // Valider la taille (max 5MB)
  if (file.size > 5 * 1024 * 1024) {
    throw new Error('L\'image ne doit pas dpasser 5MB');
  }

  try {
    // Utiliser la route publique pour l'inscription
    const formData = new FormData();
    formData.append('image', file);
    
    const data = await apiUpload('/api/upload/image/public', formData);
    
    if (!data.success) {
      throw new Error(data.message || 'Erreur lors de l\'upload');
    }
    
    // Retourner l'URL
    const imageUrl = data.data?.url || data.data?.path || '';
    
    if (!imageUrl) {
      throw new Error('Aucune URL d\'image valide reue du serveur');
    }

    // Pas de limite de longueur - stock comme TEXT dans la DB
    return imageUrl;
  } catch (error: any) {
    console.error('Erreur upload avatar public:', error);
    throw new Error(error.message || 'Impossible d\'uploader l\'avatar');
  }
};